import React from 'react'
import { Link, useLocation } from 'react-router-dom'
import { useNavigate } from "react-router";
const Navbar = () => {
  let navigate = useNavigate()
  const handleLogout = () =>{
    localStorage.removeItem('token');
    navigate('/login')
  }
  const location = useLocation();
  return (
    <nav className="navbar navbar-expand-lg bg-body-tertiary navbar-dark bg-dark">
    <div className="container-fluid">
      <Link className="navbar-brand fw-bold" to="/">Notes-Book</Link>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <Link className= {`nav-link ${location.pathname === "/"? "active" : ""} mx-3 fw-bold`} aria-current="page" to="/">Home</Link>
          </li>
          <li className="nav-item">
            <Link className={`nav-link ${location.pathname === "/about"? "active" : ""} fw-bold`} to="/about">About</Link>
          </li>
        </ul>
       {!localStorage.getItem('token')?<form className="d-flex" role="search">
        <Link className="btn btn-primary mx-2" to="/login" role="button">Login</Link>
        <Link className="btn btn-primary mx-2" to="/signup" role="button">Signup</Link>
        </form>:
        <div>
        <Link className="btn btn-primary mx-2" to="/profile" role="button">Profile</Link>
          <button onClick={handleLogout} className='btn btn-primary'>Logout</button>
        </div>
          }
      </div>
    </div>
  </nav>
  )
}
export default Navbar
